<template>
    <td :class="{start: start, 
        end: destination, 
        visited: !start && !destination && visited, 
        wall: wall,
        path: !destination && inPath}"
        v-on:click="wallToggle" 
        :draggable="start || destination" @dragstart="dragStart"
        @drop="drop" @dragover="allowDrop" 
        >
    </td>
</template>

<script>
export default {
    name: 'Node',
    props:[
        'uid'
    ],
    data() {
        return {
            children:[],
            start: false,
            destination: false,
            visited: false,
            inPath: false,
            wall: false,
            queued: false,
            path:[],
            weight: 100
        }
    },

    methods:{
        getWeight() {
            if(this.uid[0] == 4 && this.uid[1] == 10) {
                return 100
            } else if(this.uid[0] == 5 && this.uid[1] == 10) {
                return 100
            }else {
                return 100
            }
        },
        dragStart(ev) {
            let type
            if(this.start) {
                type = 'start'
                //this.$set(this, 'start', false)
            } else if(this.destination) {
                type = 'destination'
                //this.$set(this, 'destination', false)
            }
            ev.dataTransfer.setData("srcType", type)
        },
        allowDrop(ev){
            console.log(this.destination, "DES", this.start)
            if(this.start == true) {
                this.$set(this, 'start', false)
            } else if (this.destination == true) {
                this.$set(this, 'destination', false)
            }
            ev.preventDefault();
        },
        drop(ev){
            ev.preventDefault();
            let type = ev.dataTransfer.getData('srcType')
            console.log(type, "t")
            if(type == 'start') {
                this.$emit('node-action', 'setStart', this.uid)
                this.$set(this, 'start', true)
            } else if(type == 'destination') {
                this.$emit('node-action', 'setDestination', this.uid)
                this.$set(this, 'destination', true)
            }
            
        },
        setChildren(nodes) {
            let id = this.uid
            let row = id[0]
            let col = id[1]
            let children = []
            if (nodes[row][col-1] && !nodes[row][col-1].wall){
                children.push([row,col-1])
            }
            if (nodes[row][col+1] && !nodes[row][col+1].wall){
                children.push([row,col+1])
            }
            if (nodes[row+1] && nodes[row+1][col] && !nodes[row+1][col].wall) {
                children.push([row+1,col])
            }
            if (nodes[row-1] && nodes[row-1][col] && !nodes[row-1][col].wall) {
                children.push([row-1,col])
            }
            this.$set(this, 'children', children)
        },
        wallToggle() {
            
            if(!this.start && !this.destination) {
                if(this.wall) {
                    this.$set(this,'wall',false)
                    this.$emit('update-nodes', this)
                } else {
                    this.$set(this,'wall',true)
                    this.$set(this,'children', [])
                    this.$emit('update-nodes', this)
                }
            }
            
        },
        clearAll() {
            if(this.wall) {
                this.$set(this,'wall', false)
                this.$emit('update-nodes', this)
            }
            this.$set(this,'visited', false)
            this.$set(this,'inPath', false)
            this.$set(this,'queued', false)
            this.$set(this,'path', [])
        }
    },
    created() {
        if(this.uid[0] == 4 && this.uid[1] == 5){
            this.$set(this,'start', true)
        }

        if(this.uid[0] == 7 && this.uid[1] == 30){
            this.$set(this,'destination', true)
        }
        this.$emit('update-nodes', this)
    }
}
</script>
<style scoped>
.start{
    background-color: green
}
.end {
    background-color: red
}
.visited {
    background-color: yellow
}

.path {
    background-color: skyblue
}

.wall {
    background-color: #717e91;
}

.dotted {
    border:1px dotted black
}
</style>