
export default async function() {
    let queue = []
    let startNode = this.nodes[this.start[0]][this.start[1]]
    let found = false
    queue.push(startNode)
    while(!found) {
        if(queue.length == 0) {
            found = true
            break;
        }
        if(queue[0].destination){
            found = true
            for (let i in queue[0].path){
                let path = queue[0].path
                await sleep(2)
                this.nodes[path[i][0]][path[i][1]].inPath = true
            }             
            break;
        } else {
            let popped = queue.splice(0, 1) // FIFO from queue
            popped = popped[0]
            popped.$set(popped,'visited',true)
            await sleep(1)
            if(popped.children.length > 0) {
                popped.children.forEach(child => {
                    let row = child[0]
                    let col = child[1]
                    let childNode = this.nodes[row][col]
                    if (!childNode.queued) {
                        childNode.queued = true
                        childNode.path = popped.path ? popped.path.concat([childNode.uid]) : [childNode.uid]
                        queue.push(childNode)
                    }
                });
            }
        
        }
        
    }
    
}
    


function sleep(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
}