<template>
    <div>
      <b-navbar toggleable="lg" type="dark" variant="dark">
        <b-navbar-brand href="#">PathFinding Visualizer</b-navbar-brand>
        <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

        <b-collapse id="nav-collapse" is-nav>
          <b-navbar-nav>
            <b-nav-item-dropdown :text="dropDownText">
              <b-dropdown-item @click="algoSelection('bfs')">Breadth-First-Search</b-dropdown-item>
              <b-dropdown-item @click="algoSelection('dfs')">Depth-First-Search</b-dropdown-item>
              <b-dropdown-item @click="algoSelection('a*')">A*</b-dropdown-item>
              <b-dropdown-item @click="algoSelection('dijkstra')">Djithstra's</b-dropdown-item>
            </b-nav-item-dropdown>
          </b-navbar-nav>
          <b-button size="sm" class="my-2 my-sm-0 visualize-btn" v-on:click="visualize">Visualize</b-button>
          <b-navbar-nav class="ml-auto">
            <b-button size="sm" @click="clear">Clear</b-button>
          </b-navbar-nav>
        </b-collapse>
      </b-navbar>
    </div>
</template>

<script>
export default {
    data() {
        return {
            dropDownText: 'Select Algorithm'
        }
    },
    methods:{
        visualize(){
            this.$emit('header-action', {type: 'visualize', value: this.dropDownText})
        },
        algoSelection(algo) {
            if(algo == 'bfs') {
                this.dropDownText = 'Breadth-First-Search'
            } else if (algo == 'dfs'){
                this.dropDownText = 'Depth-First-Search'
            } else if (algo == 'dijkstra'){
                this.dropDownText = 'Dijkstra'
            }
        },
        clear() {
            this.$emit('header-action', {type: 'clearMaze'})
        },
        setStart() {
            this.$emit('header-action', {type: 'setStart'})
        },
        setDestination() {

        }
    }
}
</script>
<style scoped>
.visualize-btn {
    margin:auto;
}
