<template>
  <div class="container-fluid maze-container">
    <table class="maze">
      <tr v-for="row in rows" v-bind:key="row">
        <Node v-for="col in columns" :key="col" :uid="[row,col]" v-on:update-nodes="updateChildren"
        @node-action="nodeAction"></Node>
      </tr>
    </table>
  </div>
</template>

<script>
import Node from './Node.vue'
import PathAlgo from '../services/algos/index.js'
export default {
  name: 'Maze',
  components: {
    Node
  },
  props:['headerAction'],
  mixins:[PathAlgo],
  data() {
    return {
      rows: 20,
      columns: 45,
      nodes: [],
      start: [4,5],
      end: [7,30]
    }
  },
  watch: {
    headerAction: function() {
      let type = this.headerAction.type
      let value = this.headerAction.value
      switch(type) {
        case 'visualize':
          if(value == 'Breadth-First-Search') {
            this.bfs()
          } else if(value == 'Dijkstra') {
            this.dijkstra()
          } else if(value == 'Depth-First-Search') {
            alert('dfs')
            this.dfs()
          }

          break;
        case 'clearMaze':
          this.clearMaze()
          break;
        case 'setStart':
          this.clearMaze()
          break;
      }
    }
  },
  updated(){
    console.log(this.headerAction, "HAA")
  },
  methods:{
    updateNodes(node) {
      let id = node.uid
      let row = id[0]
      let col = id[1]
      if(!this.nodes[row]){
        this.nodes[row] = []
      } 
      this.nodes[row][col] = node
      if(this.nodes[row-1] && this.nodes[row-1][col]) {
        this.nodes[row-1][col].setChildren(this.nodes)
      }
      if(this.nodes[row] && this.nodes[row][col-1]) {
        this.nodes[row][col-1].setChildren(this.nodes)
      }
    },
    updateChildren(node) {
      let id = node.uid
      let row = id[0]
      let col = id[1]
      if(!this.nodes[row]){
        this.nodes[row] = []
      } 
      this.nodes[row][col] = node
      if (this.nodes[row][col-1] && !this.nodes[row][col-1].wall){
        this.nodes[row][col-1].setChildren(this.nodes)
      }
      if (this.nodes[row][col+1] && !this.nodes[row][col+1].wall){
        this.nodes[row][col+1].setChildren(this.nodes)
      }
      if (this.nodes[row+1] && this.nodes[row+1][col] && !this.nodes[row+1][col].wall) {
        this.nodes[row+1][col].setChildren(this.nodes)
      }
      if (this.nodes[row-1] && this.nodes[row-1][col] && !this.nodes[row-1][col].wall) {
        this.nodes[row-1][col].setChildren(this.nodes)
      }
      if(!node.wall) {
        node.setChildren(this.nodes)
      }
    },
    clearMaze() {
      this.nodes.forEach(row =>{
        row.forEach(node => {
          node.clearAll()
        })
      })
    },
    nodeAction(type, payload) {
      console.log(type, payload, "TPPPP")
      if(type == 'setStart') {
        this.start = payload
      } else if(type == 'setDestination') {
        this.end = payload
      }
    }
  },
  mounted(){
    console.log(this.nodes, "FNAL")
  }
}
</script>

