<template>
  <div  id="app">
    <NavBar @header-action="headerAction" />
    <Maze :header-action="payload" />
  </div>
</template>

<script>
import NavBar from './components/NavBar.vue'
import Maze from './components/Maze.vue'

export default {
  name: 'app',
  data() {
    return {
      payload: {}
    }
  },
  components: {
    Maze,
    NavBar
  },
  methods:{
    headerAction(payload) {
      this.payload = payload
    }
  }
}
</script>

