let priorityQueue = []
window.pw = priorityQueue
console.log(priorityQueue, "PQ")
priorityQueue.__proto__.append = function(node) {
    this.push(node)
    if(this.length > 1) {
        this.siftUp()
    }
}

priorityQueue.__proto__.delete = function(node) {
    console.log(node)
    if(this.length == 1) {
        return this.pop()
    } else if(this.length > 1) {
        let first = this[0]
        let last = this[this.length - 1]
        if(first.getWeight() != last.getWeight()) {
            this[0] = last
            this[this.length - 1] = first
            let popped = this.pop()
            this.siftDown()
            return popped
        } else {
            let popped = this.splice(0, 1)
            return popped[0]
        }
    }
}

priorityQueue.__proto__.siftUp = function() {
    let index = this.length - 1 
    while (index > 0 ) {
        let parentIndex = Math.floor((index-1)/2)
        if(this[parentIndex].getWeight() < this[index].getWeight()){
            let temp = this[parentIndex]
            this[parentIndex] = this[index]
            this[index] = temp
        }
        index = parentIndex
    }
}

priorityQueue.__proto__.siftDown = function() {
    let i = 0
    let lastParent = Math.floor(this.length -1 /2)
    while(i <= lastParent && this[2*i + 1]) {
        let indexToComp = this[2*i + 1].getWeight() >= (this[2*i + 2] ? this[2*i + 2].getWeight() : -1) ? 2*i + 1 : 2*i + 2
        let elemToComp = this[indexToComp].getWeight()
        if(this[i].getWeight() <= elemToComp) {
            let temp = this[i]
            this[i] = this[indexToComp]
            this[indexToComp] = temp
            i = indexToComp
        } else {
            return
        }
    }
    
}


export default async function() {
    let startNode = this.nodes[this.start[0]][this.start[1]]
    let found = false
    console.log(startNode,found, "SF")
    //startNode.weight = 0
    priorityQueue.append(startNode)
    while(!found) {
        if(priorityQueue.length == 0) {
            found = true
            break;
        }
        if(priorityQueue[0].destination){
            found = true
            for (let i in priorityQueue[0].path){
                let path = priorityQueue[0].path
                await sleep(2)
                this.nodes[path[i][0]][path[i][1]].inPath = true
            }             
            break;
        } else {
            let popped = priorityQueue.delete() // FIFO from queue
            popped.$set(popped,'visited',true)
            await sleep(1)
            if(popped.children.length > 0) {
                popped.children.forEach(child => {
                    let row = child[0]
                    let col = child[1]
                    let childNode = this.nodes[row][col]
                    if (!childNode.queued) {
                        childNode.queued = true
                        childNode.path = popped.path ? popped.path.concat([childNode.uid]) : [childNode.uid]
                        priorityQueue.append(childNode)
                    }
                });
            }
        
        }
        
    }
}


function sleep(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
}