
export default async function() {
    let stack = []
    let startNode = this.nodes[this.start[0]][this.start[1]]
    let found = false
    stack.push(startNode)
    while(!found) {
        if(stack.length == 0) {
            found = true
            break;
        }
        if(stack[stack.length-1].destination){
            console.log(stack[stack.length-1].path, "SPPPP")
            found = true
            stack[stack.length-1].path.forEach(async (path) => {
                //console.log("SSSS>>>", path, "STKKK")
                await sleep(2)
                this.nodes[path[0]][path[1]].inPath = true
            })             
            break;
        } else {
            let popped = stack.pop()
            popped.$set(popped,'visited',true)
            console.log(popped.uid, "PPPPPPPP", popped.path)
            await sleep(1)
            if(popped.children.length > 0) {
                popped.children.forEach(child => {
                    let row = child[0]
                    let col = child[1]
                    let childNode = this.nodes[row][col]
                    console.log(childNode.uid, childNode.queued, "CCC")
                    if (!childNode.queued) {
                        childNode.queued = true
                        console.log("setting path", popped.path[popped.path.length-1])
                        childNode.path = popped.path ? popped.path.concat([childNode.uid]) : [childNode.uid]
                        stack.push(childNode)
                    }
                });
            }
        
        }
        
    }
    
}
    


function sleep(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
}